import React, {useState, useContext} from "react";

import { UserContext } from "../UserContext";

const Home = () => {

    const {user, setUser} = useContext(UserContext);

    let homeSplash = '';

    if(user != null){
    homeSplash = (
        <div>Hello {user['username']} </div>)
    }
    else{
        homeSplash = (
            <div>Hello welcome to the site</div>)
    }



    return (
        <div>{homeSplash}</div>
    );
};

export default Home;