import React, { Component } from 'react';

class Card extends Component{
  constructor(props){
    super(props);
    this.state = {};
  } 
  render () {
          
    const { card } = this.props;
    let draw = '';
    if(card.Url)
    {
      draw = (<img className="game-card" src={card.Url}/>);
    }
    return (
       <div>{draw}</div>
    );
  }

}
export default Card;  