import React, {useState, useContext} from "react";
import { Link } from "react-router-dom";
import { UserContext } from "../UserContext";

const Nav = () => {

const { user, setUser} = useContext(UserContext);

let navBar = '';
if(user != null)
{
  navBar = (
<ul className="navbar-nav">
        <li className="nav-item">
          <Link className="nav-link" to="/logintest">logintest</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/player">Player</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/usersettings" aria-disabled="true">User Settings</Link>
        </li>
      </ul>
  )
}
else{
navBar = (
<ul className="navbar-nav">
        <li className="nav-item">
          <Link className="nav-link" to="/logintest">logintest</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link disabled" to="#"aria-disabled="true">Disabled</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link" to="/player">Player</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/login" aria-disabled="true">login</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/register" aria-disabled="true">signup</Link>
        </li>
      </ul>
)
}

return(
    
    <nav className="navbar navbar-expand-lg navbar-dark" style={{backgroundColor:'#808080'}}>
    <div className="container-fluid">
      <Link to="/"  className="navbar-brand" >Navbar</Link>
      
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      
      <div className="collapse navbar-collapse" id="navbarNav">
        {navBar}
      </div>
    </div>
    
  </nav>
);

};

export default Nav;