import logo from './logo.svg';
import './App.css';
import Login from './pages/LoginPage';
import Nav from './components/Nav.js';
import Register from './pages/Register';
import Home from './pages/Home';
import {BrowserRouter, Route} from 'react-router-dom';
import React, { useContext, useState, useMemo } from 'react';
import LoginTest from './pages/LoginTest';
import { UserContext } from "./UserContext";
import UserSettings from './pages/UserSettings';
import { axios } from "./axios";
import PlayerTest from './pages/PlayerTest';
import LoginPage from './pages/LoginPage';

function App() {
    const [user, setUser] = useState(null);

   if(localStorage.getItem('user') && user == null)
   {
    setUser(JSON.parse(localStorage.getItem('user')));
   }


  // //check if user saved in cookies
  // if(user != null)
  // {
  //   console.log("user is:" + JSON.stringifyuser);

  //   if(user != null)
  //   {
  //     axios.post('Users/authenticate',{
  //       Username: user['username'],
  //       Token: user['token']
  //     })
  //     .then((response) => {
  //       console.log("User Authed"+ response);
      
  //       //set the variable that will be set in synch

  //     }, (error) => { console.log("Error"); });
  //     //set user here

  //   }
  // }

 // const providerValue = useMemo(() => ({value,setValue}),[value,setValue]);



  return (
    <div className="App" >
    <UserContext.Provider value={{user, setUser}}>
    <BrowserRouter>

      <div className="container"style={{backgroundColor:'#d3d3d3'}}>
          <Nav  />


      <main className="form-signin">
      
      
        <Route path="/" exact component={Home}  />

        <Route path="/login" component={LoginPage}  />
        <Route path="/register" component={Register}  />
        <Route path="/usersettings" component={UserSettings}  />
        <Route path="/logintest" component={LoginTest}  />
        <Route path="/player" component={PlayerTest}  />

      </main>
      
      </div>
        </BrowserRouter>
        </UserContext.Provider>
    </div>
    
  );

}

export default App;

