import React, {useState, useContext} from "react";
import {Redirect} from 'react-router-dom';

import { UserContext } from "../UserContext";
import { axios } from "../axios";

const Register = () => {

    //useState returns an array, we destructure it into a variable and function
    const [userName, setUserName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [tag,setTag] = useState('');
    const [clanTag, setClanTag] = useState('');

    const {user, setUser} = useContext(UserContext);

    //check legitimacy of either user or clan Tag
    const makePostRequest = (e) => {
        e.preventDefault();
       
        axios.post('Users/signup', {
            Username: userName,
            Password: password,
            Email: email,
            Tag: tag,
            ClanTag: clanTag
          })
          .then((response) => {
            console.log(response);
          }, (error) => {
            console.log(error);
          });
        }

    let register = '';

    if(user==null){
        register = (
            <form onSubmit = {makePostRequest}>
                <h1 className="h3 mb-3 fw-normal">Please Register</h1>

                <span><b>Username</b></span>
                <input type="username" id="inputUsername" className="form-control" placeholder="Username" onChange={ e => setUserName(e.target.value)} required />

                <span><b>Password</b></span>
                <input type="password" id="inputPassword" className="form-control" placeholder="Password" onChange={e => setPassword(e.target.value)} required />

                <span><b>Email</b></span>
                <input type="email" id="inputEmail" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)}required />

                <span><b>Password</b> (7 characters or more)</span>
                <input id="inputTag" className="form-control" placeholder="#3x4mp13" onChange={e => setTag(e.target.value)}required />

                <button className="w-100 btn btn-lg btn-primary" type="submit">Sign in</button>
            </form>);
    }
    else {register =(<Redirect to="/" />);}






    /*
    const submit = (event) =>{
        console.log({name,email,password});
        event.preventDefault();
        axios.get('')
    }
*/


    return (
        <div>
            {register}
        </div>
    );
};

export default Register;