import React, { useState, useContext } from "react";
import { Redirect } from 'react-router-dom';

import { UserContext } from "../UserContext";
import { axios } from "../axios";

import Deck from "../components/Deck";
import Card from "../components/Card";


const LoginTest = () => {

    const { user, setUser } = useContext(UserContext);
    const [deck, setDeck] = useState([]);
    const [player, setPlayer] = useState({});
    const [cards, setCards] = useState([]);
    const config = { headers: { Authorization: `bearer ${user['token']}`}};
    //const config = {headers: { "Authorization": "Bearer 59a5cfdc-be68-4830-8cb8-d03ae8048440c54e2957-ca79-4453-b22d-b9706f74c89f"}};
    var bodyParameters = {
        "key": "value"
    }

    ////// THERE ARE THREE VERSIONS OF getBattles, USE THE ONE YOU NEED, DELETE THE OTHERS TO REDUCE COGNITIVE LOAD

    async function getBattles() {
        const response = await axios.get("cards");
        // if you want to just set the battles from the result, use this:
        setCards(response.data
        );

    }

    async function getDeck()
    {
        const response = await axios.get("decks/1",config);
        let tempDeck = response.data;
        let returnDeck = [];
        
        for(let i = 0; i < 8; i++)
        {
        if(!tempDeck["Card"+(i+1)]){
            returnDeck["Card"+(i+1)] = await getCard(tempDeck["Card"+(i+1)+"Id"])
        }
        }
    
        setDeck(returnDeck);
    }

    async function getCard(id)
    {
        const response = await axios.get("cards/"+id,config);
        return response.data;
    }
    async function getPlayer()
    {
        const response = await axios.post("players/update",user,config);
        console.log(response);
        setPlayer(response.data);
    }

    let getBattlesButton = (<button onClick={getDeck}>click</button>);


    let draw = '';

    if (deck["Card1"]) {
        draw = (<Deck deck={deck} />);
    }
    else draw =(getBattlesButton)

    console.log(deck)

    //<p> { player!=null ? JSON.stringify(player): getBattlesButton}</p>
    return (
        <div>
            {draw}
        </div>
    );

};


export default LoginTest;