import React, { useState, useContext } from "react";
import { Redirect } from "react-router-dom";

import { UserContext } from "../UserContext";
import { Login } from "../utilities/scripts";

const LoginPage = () => {
  //useState returns an array, we destructure it into a variable and function
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const { user, setUser, player, setPlayer } = useContext(UserContext);

  let login = "";

  const makePostRequest = (e) => {
    e.preventDefault();

    if (document.getElementById("stayLoggedIn").checked) {
        localStorage.setItem("user", JSON.stringify(user));

    } 

  };

  if (user == null) {
    login = (
      <form onSubmit={makePostRequest}>
        <h1 className="h3 mb-3 fw-normal">Please Register</h1>

        <input
          type="username"
          id="inputUsername"
          className="form-control"
          placeholder="Username"
          onChange={(e) => setName(e.target.value)}
          required
        />

        <input
          type="password"
          id="inputPassword"
          className="form-control"
          placeholder="Password"
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        <input id="stayLoggedIn" type="checkbox"></input>

        <button className="w-100 btn btn-lg btn-primary" type="submit">
          Sign in
        </button>
      </form>
    );
  } else {
    login = <Redirect to="/" />;
  }

  return <div>{login}</div>;
};

export default LoginPage;
