import React, { Component } from "react";
import Deck from "./Deck";

class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { battle } = this.props;
    let draw = "";
    //only rendering 1v1 Battles
    if(battle.Team1DeckBId == 0 && battle.Team1DeckAId !=0)
    {
      draw = (
        <div className="card">
          <div className="row d-inline-block">
            <div className="col-5 d-inline-block">
              <p>
                <b>{battle.Team1Name}</b>
              </p>
              <p>
                <b>Crowns:</b>{battle.Team1Crowns}
              </p>
              <p>
                <b>deck</b><Deck deck={battle.deck}/>
              </p>
             
            </div>
  
            <div className="col-2 d-inline-block">
              <h1>VS</h1>
            </div>
  
            <div className="col-5 d-inline-block"> 
            <p>
                <b>{battle.Team2Name}</b>
              </p>
              <p>
                <b>Crowns:</b>{battle.Team2Crowns}
              </p>
          </div>
          </div>   
        </div>
      );  
    }
    

    return <div className="text-center">{draw}</div>;
  }
}
export default Card;
