import React, { Component } from 'react';

class Chest extends Component{
  constructor(props){
    super(props);
    this.state = {};
  } 
  render () {
          
    const { chest } = this.props;
    let draw = '';
    if(chest.Name)
    {
      draw = (<div id={"chest-"+chest.Index}><p><b>{chest.Index+1}</b></p>
        <img className="game-card" src={chest.Url}/>
      <p><b>{chest.Name}</b></p>
      </div>);
    }
    return (
       <div>{draw}</div>
    );
  }

}
export default Chest;  