import React, { useState, useContext } from "react";
import { Redirect } from 'react-router-dom';

import { UserContext } from "../UserContext";
import { axios } from "../axios";
    
import Battle from "../components/Battle";
import Player from "../components/Player";
import ChestCollection from "../components/ChestCollection";


const PlayerTest = () => {

    const { user, setUser } = useContext(UserContext);
    
    const [player, setPlayer] = useState([]);
    const [deck, setDeck] = useState([]);

    const config = { headers: { Authorization: `bearer ${user['token']}`}};

    async function getCard(id)
    {
        const response = axios.get("cards/"+id,config);
        return response.data;
    }
    async function getPlayer()
    {
        const response = await axios.post("players/update",user,config);
        

        
        setPlayer(response.data);

    }

    let getBattlesButton = (<button onClick={getPlayer}>click</button>);
    let draw ='';
    
    if(player["Name"])
    {
        draw = (<div className="text-center">
            <ChestCollection chestCollection={player.Chests}/>
        <Player player={player}/>
        <Battle battle={player.Battles[0]}/>
        </div>);
        console.log(player);
    }
    else{
    draw=(getBattlesButton);
}

        
    return (
        <div>
            {draw}
        </div>
    );

};


export default PlayerTest;